:root {
  --theme: rgb(52, 73, 94);
  --theme-dark: rgb(42, 62, 81);
  --green: rgb(55, 215, 183);
  --green-dark-1: rgb(47, 204, 173);
  --green-dark-2: rgb(103, 151, 142);
  --red: rgb(255, 65, 65);
  --red-dark-1: rgb(239, 58, 58);
  --gray-1: rgb(239, 239, 239);
  --gray-2: rgb(234, 234, 234);
  --gray-3: rgb(223, 223, 223);
  --gray-4: rgb(160, 160, 160);
  --gray-4-transparent: rgba(160, 160, 160, .05);

  --shadow-1: 0 0 2px rgba(0, 0, 0, .1);
  --shadow-2: 0 0 2px rgba(0, 0, 0, .3);
  --shadow-3: 0 0 3px rgba(0, 0, 0, .3);
  --shadow-4: 0 0 5px rgba(0, 0, 0, .3);
  --shadow-5: 0 0 8px rgba(0, 0, 0, .3);

  --font-logo: 'Sriracha', cursive;
  --font-home-heading: 'Handlee', cursive;
  --font-heading: 'Roboto', sans-serif;
  --font-main: 'Lato', sans-serif;

  --z-index-1: 10;
  --z-index-2: 100;
  --z-index-3: 1000;
  --z-index-4: 10000;
  --z-index-5: 100000;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

body, ul, ol, h1, h2, h3, h4, h5, h6, p {
  margin: 0;
  padding: 0;
}

html {
  font-size: 62.5%;
  background-color: rgb(52, 73, 94);
}

@media only screen and (min-width: 1800px) {
  html { font-size: 68.75%; }
}

@media only screen and (max-width: 900px) {
  html { font-size: 56.25%; }
}

@media only screen and (max-width: 600px) {
  html { font-size: 50%; }
}

body {
  color: #fff;
  margin: 0;
  font-family: var(--font-main);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body::-webkit-scrollbar {
  height: 19px;
  width: 19px;
}

body::-webkit-scrollbar-track {
  background-color: var(--theme);
  border-left: 1px solid var(--theme-dark);
  box-shadow: inset 0 0 5px var(--theme-dark);
}

body::-webkit-scrollbar-thumb {
  background-color: var(--green);
  border-radius: 10px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
  monospace;
}

.LoaderWrapper {
  text-align: center;
  margin-top: 5rem;
}

